@use "../base/variables" as v;
.hp-multimedia {
  padding: 4rem 0;
  background: v.$gray-bg;
  &-list {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;

    img {
      max-width: 100%;
      height: auto;
      font-size: 0;
    }

    li {
      position: relative;
      display: flex;
      flex-direction: column;
      line-height: 1.5;
      border-radius: 2px;
      background-color: v.$white;
      box-shadow: 0px 4px 8px rgba(10, 8, 59, 0.1);
      transition: transform 0.2s ease, box-shadow 0.2s ease;
      margin-bottom: 2rem;

      &:nth-child(1) {
        a:first-child {
          border-bottom: 2px solid v.$dsa-yellow;
        }
      }

      &:nth-child(2) {
        a:first-child {
          border-bottom: 2px solid v.$dsa-red;
        }
      }

      &:nth-child(3) {
        a:first-child {
          border-bottom: 2px solid v.$dsa-green;
        }
      }

      &:nth-child(4) {
        a:first-child {
          border-bottom: 2px solid v.$dsa-purple;
        }
      }

      span {
        font-weight: bold;
        font-size: 0.625rem;
        color: v.$dsa-light-grey;
        text-transform: uppercase;
        letter-spacing: 1px;
        padding: 1rem 10px 0.5rem 10px;
      }

      a {
        &:first-child {
          overflow: hidden;
        }

        &:last-child {
          color: v.$black;
          padding: 0 10px 20px 10px;
        }
      }

      &:hover {
        box-shadow: 0 13px 14px 0 rgba(10, 8, 59, 0.1);
        transform: translateY(-5px);
      }
    }
  }
}

@media (min-width: 768px) {
  .hp-multimedia {
    &-list {
      li {
        width: calc((100% - 30px) / 2);
      }
    }
  }
}
@media (min-width: #{v.$mobile-bp-min}) {
  .hp-multimedia {
    &-list {
      li {
        width: calc((100% - 90px) / 4);

        a {
          &:first-child {
            max-height: 180px;
          }
        }
      }
    }
  }
}
